.container{height:100vh;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.innerContainer{display:flex;flex-direction:column;align-items:center}.logo{transition:var(--defaultTransition)}.logo:hover,.logo:focus{transform:scale(1.1)}.or{color:var(--gray);margin-bottom:30px}.or::after{content:"————————";margin-left:20px;width:128px;height:1px;color:#c7c7c7}.or::before{content:"————————";margin-right:20px;width:128px;height:1px;color:#c7c7c7}.accountContainer{display:flex;flex-direction:column;align-items:center;justify-content:center}.account{margin-top:15px}.link{margin-right:30px}